import * as axios from 'axios';
import * as qs from 'querystring';

export class AxiosForm{
    static GetIns(){
        return axios.create({
            headers: {'Content-Type': 'application/x-www-form-urlencoded'},
            transformRequest: [function (data) {
                // form-dataQsaxios
                let info = qs.encode(data);
                return info;
            }],
        });
    }
}


